.navroot{
    
    display:flex;
   
    width:100vw;
    justify-content: center;
    
}


.mainNav{
    width:80vw;
display:flex;
align-items: flex-end;
justify-content: space-around;

}



.groupChat{
    margin:0;
    color:rgb(170, 170, 170);
    font-size: 0.7rem;;
    /* border:1px solid #48aca7; */
    /* border-bottom:transparent; */
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
}

.navButtonContainer{
    display: flex;
    justify-content: space-around;
    /* border:1px solid #48aca7; */
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    /* border-bottom:1px solid #48aca7;
    border-left:1px solid #48aca7;
    border-right:1px solid #48aca7; */
    width:10rem;
}

.navButtons{
    border-top-left-radius: 0.2rem;
    border-top-right-radius: 0.2rem;
    /* font-weight: bold; */
    outline: none;
    padding:0 0.5rem;
    margin:0.0rem 0.5rem;
    border:none;
}

