}

@media (min-width:500px){
    .hideButton{
        display:none;
    }
    .wideButton{
        display:none;
    }
    .mainNavHidden{
        width:80vw;
display:flex;
align-items: flex-end;
justify-content: space-around;
        max-width: 35rem;
    }
    .mainNav{
        max-width:35rem;
    }
}

@media screen and (max-width:625px) {
   
    .navButtons{
        margin:0;
        font-size:0.8rem;
    }
    .groupChat{
        font-size: 0.6rem;
    }
  
    
}


@media (max-width:500px) {
    .mainNav{
        position:absolute;
        display:block;
        width:5rem;
        background: #006D77;
        z-index: 1;
    }
    .navroot{
        justify-content: flex-end;
    }
    .navButtonContainer{
        display:block;
    }
    .dropDown{
        display:block;
    }
    .mainNavHidden{
        display:none;
    }
    .wideButton{
        width:5rem;
    }
    .navButtonContainer{
        /* border:1px solid white; */
        border-radius:0;
        width:100%;
        margin:0;
        padding:0;
    }
    .navButtons{
        border-radius: 0;
        width:100%;
        border-bottom:1px solid #018d81;
        margin:0;
        padding:0;
        /* font-size:0.8rem; */
        /* border-top:1px solid white; */
        /* border-bottom:1px solid white; */
    }
    .homehome,.homefriends,.homefriend,.homemail{
        border-bottom:none;
    }
}